.wrapper {
  margin-top: 184px;
  margin-bottom: 26px;
  min-height: 850px;
  width: 100%;
}

.container{
  position: relative;
  padding-bottom: 90px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: var(--white);
  min-height: 850px;
}

.orders-title {
  padding: 23px 24px;
  border-bottom: 1px solid var(--white-three);
}

.orders-title-item {
  font-size: 12px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: var(--greyish);
}

.orders-body {
  padding: 14px 24px;
  align-items: center;
  &.dark-theme{
    background-color: var(--white-two);
  }
}

.orders-body-item {
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: var(--charcoal-grey);
  word-break: break-all;
}

.dropdown-icon {
  font-size: 13px;
}

.status {
  display: inline-block;
  border-radius: 12px;
  padding: 5px 7px;
  font-size: 12px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: normal;
  text-align: center;
  color: var(--white);
  width: 90px;
}

.pending {
  background-color: #fbba4e;
}
.completed {
  background-color: var(--pea-green);
}
.canceled {
  background-color: #f05d62;
}

.status-wrapper {
  position: relative;
}

.status-header:hover .status-options{
  display: block;
}

.status-options {
  display: none;
  position: absolute;
  right: 0;
  top: 15px;
  padding: 16px;
  border-radius: 2px;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.08);
  background-color: var(--white);
  width: 193px;
  z-index: 10;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  padding: 8px 0;
  color: var(--charcoal-grey);
  &:hover{
    background-color: var(--white-three);
    cursor: pointer;
  }
}

.status-item ~ .status-item{
  // margin-top: 16px;
}

.status-color {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.footer {
  position: absolute;
  display: flex;
  justify-content: space-between;
  bottom: 38px;
  right: 0;
  padding-left: 24px;
  padding-right: 24px;
  width: 100%;
  justify-content: space-between;
}

.footer-info {
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: var(--charcoal-grey);
}

.page,
.page-list {
  display: flex;
}

.page-item,
.page-dropdown-item {
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  border: solid 1px var(--white-three);
  background-color: var(--white);
  &.active {
    border: solid 1px var(--pale-orange);
    background-color: var(--pale-orange);
    color: var(--white);
  }
}

.page-dropdown-item {
  width: 48px;
  height: 32px;
  margin-right: 20px;
  display: flex;
  justify-content: space-around;
}

.page-icon-dropdown{
    padding-top: 8px;
}